<template>
  <div>
    <!-- toolbar -->
    <v-card>
      <v-toolbar class="lighten-4" flat>
        <a href="/taipei/main" style="text-decoration: none; color: black">
          <div class="d-flex align-center">
            <img
              class="ml-3"
              src="https://i.imgur.com/pqFXZCj.jpg"
              width="130px"
              height="60px"
            />
            <i>
              <h1 class="ml-2">台北旅遊</h1>
            </i>
          </div></a
        >

        <v-spacer />
        <!-- 登入/註冊 -->
        <div v-if="!userId">
          <v-btn class="mr-2" depressed @click="memberInfo('login')"
            >登入</v-btn
          >
          <v-btn class="mr-2" depressed @click="memberInfo('register')"
            >註冊</v-btn
          >
        </div>
        <!-- 用戶名稱/登出 -->
        <div v-if="userId">
          <span class="mr-4" style="font-weight: bold"
            >用戶名稱
            <span class="ml-1" style="color: #c86fc9">{{ userName }}</span>
          </span>
          <v-btn class="mr-2" depressed @click="logout()">登出</v-btn>
        </div>

        <v-btn class="mr-2" depressed @click="goSetting()">會員專區</v-btn>
      </v-toolbar>
    </v-card>

    <Dialog :dialogInfos="dialogInfos" />
  </div>
</template>

<script>
import Dialog from "./Dialog";
import { mapGetters, mapMutations } from "vuex";
export default {
  components: {
    Dialog,
  },
  data() {
    return {
      dialogInfos: { dialog: false, method: "" },
    };
  },

  methods: {
    ...mapMutations(["clearUserInfo"]),
    //會員Dialog
    memberInfo(type) {
      if (type == "login") {
        this.dialogInfos["dialog"] = true;
        this.dialogInfos["method"] = "login";
      } else if (type == "register") {
        this.dialogInfos["dialog"] = true;
        this.dialogInfos["method"] = "register";
      }
    },
    goSetting() {
      this.$router.push({ name: "memberSetting" }).catch((err) => {
        err;
      });
    },
    logout() {
      this.clearUserInfo();
    },
  },
  computed: {
    ...mapGetters(["userName", "userId"]),
  },
};
</script>
