import axios from 'axios'

export default () => {
    const headerToken = localStorage.getItem('headerToken')

    let createData = {}

    if (!headerToken) {
        createData = {
            baseURL: `https://taipei-tour.herokuapp.com/taipei`
        }
    }
    else {
        createData = {
            baseURL: `https://taipei-tour.herokuapp.com/taipei`,
            headers: { "TP-TOKEN": headerToken }
        }
    }
    return axios.create(createData)
}