<template>
  <div>
    <v-dialog
      v-model="dialog"
      :width="
        dialogInfos.method == 'register' ||
        dialogInfos.method == 'login' ||
        dialogInfos.method == 'pwd'
          ? 500
          : 700
      "
    >
      <v-card>
        <v-form ref="form">
          <!-- 登入以及註冊 -->
          <v-container
            v-if="
              dialogInfos.method == 'register' || dialogInfos.method == 'login'
            "
          >
            <v-tabs v-model="tabFllow" color="#AB47BC">
              <v-tab>登入</v-tab>
              <v-tab>註冊</v-tab>
            </v-tabs>

            <!-- 登入 -->
            <div
              v-if="
                (tabFllow == 0 && dialogInfos.method == 'login') ||
                tabFllow == 0
              "
              class="mt-5"
            >
              <v-text-field
                v-model="login.account"
                label="Email"
                :rules="[rules.email]"
                outlined
                color="#8e518d"
                dense
                autocomplete="off"
              />
              <v-text-field
                v-model="login.password"
                label="密碼"
                :rules="[rules.pwd]"
                outlined
                color="#8e518d"
                dense
                type="password"
                @keydown.enter="influence('login')"
                autocomplete="off"
              />
              <v-alert v-model="alert.login" :type="type.login" dense>{{
                message.login
              }}</v-alert>
              <v-btn
                class="mb-3"
                block
                depressed
                dark
                :loading="load.login"
                color="#AB47BC"
                @click="influence('login')"
                >登入</v-btn
              >
            </div>
            <!-- </v-form> -->
            <!-- 註冊 -->
            <!-- <v-form ref="regist"> -->
            <div
              v-if="
                (tabFllow == 1 && dialogInfos.method == 'register') ||
                tabFllow == 1
              "
              class="mt-5"
            >
              <v-text-field
                v-model="regist.account"
                label="Email"
                :rules="[rules.email]"
                outlined
                color="#8e518d"
                dense
                autocomplete="off"
              />
              <v-text-field
                v-model="regist.password"
                label="密碼"
                :rules="[rules.pwd]"
                outlined
                color="#8e518d"
                dense
                type="password"
                autocomplete="off"
              />
              <v-text-field
                label="再次輸入密碼"
                :rules="[rules.checkPwd]"
                outlined
                color="#8e518d"
                dense
                type="password"
                autocomplete="off"
              />
              <v-text-field
                v-model="regist.userName"
                label="真實姓名"
                outlined
                color="#8e518d"
                dense
                @keydown.enter="influence('regist')"
                autocomplete="off"
              />
              <v-alert v-model="alert.regist" :type="type.regist" dense>{{
                message.regist
              }}</v-alert>
              <v-btn
                class="mb-3"
                block
                depressed
                :loading="load.regist"
                dark
                color="#AB47BC"
                @click="influence('regist')"
                >註冊</v-btn
              >
            </div>
            <!-- </v-form> -->
          </v-container>

          <!-- 訂票 Dialog -->
          <v-container
            v-if="
              dialogInfos.method == 'ozo' ||
              dialogInfos.method == 'spa' ||
              dialogInfos.method == 'cat' ||
              dialogInfos.method == 'flower'
            "
            class="pa-5"
          >
            <h1 class="text-center mb-5">訂單資訊</h1>
            <v-divider class="mb-5"></v-divider>
            <!-- 商品資訊 -->
            <v-row class="d-flex align-center mb-5"
              ><v-col cols="4" class="d-flex">
                <v-icon class="mr-2" size="1.8em" color="#c86fc9"
                  >mdi-star-circle-outline</v-icon
                >
                <h2 style="color: #c86fc9" color="#c86fc9">商品名稱</h2> </v-col
              ><v-col cols="8"
                ><span style="font-size: 1.2em; font-weight: bold">{{
                  Reserve.productName
                }}</span></v-col
              ></v-row
            >
            <!-- 選擇日期 -->
            <v-row class="d-flex align-center mb-5"
              ><v-col cols="4" class="d-flex">
                <v-icon class="mr-2" size="1.8em" color="#c86fc9"
                  >mdi-star-circle-outline</v-icon
                >
                <h2 style="color: #c86fc9">選擇日期</h2> </v-col
              ><v-col cols="8">
                <v-menu
                  v-model="meunStart"
                  min-width="auto"
                  :close-on-content-click="false"
                  offset-y
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="startTime"
                      label="出發日期"
                      dense
                      readonly
                      color="#8e518d"
                      hide-details
                      outlined
                      autocomplete="off"
                      v-on="on"
                      v-bind="attrs"
                    />
                  </template>
                  <v-date-picker
                    :min="nowTime"
                    color="#c86fc9"
                    v-model="startTime"
                    @input="meunStart = false"
                    no-title
                    scrollable
                  />
                </v-menu>
              </v-col>
            </v-row>
            <!-- 付款金額 -->
            <v-row class="d-flex align-center mb-5">
              <v-col cols="4" class="d-flex">
                <v-icon class="mr-2" size="1.8em" color="#c86fc9"
                  >mdi-star-circle-outline</v-icon
                >
                <h2 style="color: #c86fc9">付款金額</h2></v-col
              ><v-col cols="8"
                ><span style="font-size: 1.4em"
                  >NT. {{ Reserve.amount }}</span
                ></v-col
              >
            </v-row>
            <!-- 付款方式 -->
            <v-row class="d-flex align-center mb-5">
              <v-col cols="4" class="d-flex">
                <v-icon class="mr-2" size="1.8em" color="#c86fc9"
                  >mdi-star-circle-outline</v-icon
                >
                <h2 style="color: #c86fc9">付款方式</h2></v-col
              ><v-col cols="8"
                ><span style="font-size: 1.4em">{{ payType }}</span></v-col
              >
            </v-row>
            <v-alert v-model="alert.purchase" :type="type.purchase" dense>{{
              message.purchase
            }}</v-alert>
            <v-btn
              block
              color="#AB47BC"
              :loading="load.purchase"
              @click="influence('purchase')"
              ><span
                style="font-size: 1.2em; font-weight: bold"
                class="white--text"
                >確認購票</span
              ></v-btn
            >
          </v-container>
          <!-- 變更密碼 Dialog -->
          <!-- <v-form ref="password"> -->
          <v-container v-if="dialogInfos.method == 'pwd'">
            <h1 class="text-center mb-5">變更密碼</h1>
            <v-text-field
              v-model="pwd.old"
              color="#8e518d"
              label="輸入舊密碼"
              outlined
              dense
              type="password"
            ></v-text-field>
            <v-text-field
              v-model="pwd.new"
              color="#8e518d"
              label="輸入新密碼"
              outlined
              dense
              type="password"
            ></v-text-field>
            <v-text-field
              label="再次輸入新密碼"
              color="#8e518d"
              :rules="[rules.checkPwd]"
              outlined
              dense
              type="password"
              @keydown.enter="influence('pwd')"
            ></v-text-field>
            <v-alert v-model="alert.pwd" :type="type.pwd" dense>{{
              message.pwd
            }}</v-alert>
            <v-btn
              outlined
              depressed
              block
              :loading="load.pwd"
              color="#AB47BC"
              class="mb-3"
              @click="influence('pwd')"
              ><span
                style="font-size: 1.2em; font-weight: bold"
                class="white--text"
                >變更密碼</span
              ></v-btn
            >
          </v-container>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import tour from "../service";
import { mapGetters } from "vuex";
export default {
  props: {
    dialogInfos: Object,
  },
  mounted() {
    this.Reserve.productName = this.goodsList[this.dialogInfos.method];
  },
  data() {
    return {
      dialog: false,
      tabFllow: "",
      meunStart: false,
      startTime: new Date(+new Date() + 8 * 3600 * 1000)
        .toISOString()
        .substr(0, 10),
      nowTime: new Date(+new Date() + 8 * 3600 * 1000)
        .toISOString()
        .substr(0, 10),
      goodsList: {
        ozo:
          "【台北最經典101一日遊】國父紀念館、四四南村、幾米月亮公車、台北101觀景台",
        cat: "【動物園、貓空一日遊】 動物園、貓空",
        spa: "【北投溫泉一日遊】 新北投車站、北投圖書館、溫泉博物館",
        flower: "【陽明山賞花一日遊】 竹子湖繡球花、小油坑、夢幻湖、冷水坑",
      },
      alert: { login: false, regist: false, pwd: false, purchase: false },
      message: { login: "", regist: "", pwd: "", purchase: "" },
      load: { login: false, regist: false, pwd: false, purchase: false },
      login: { account: "", password: "" },
      regist: { userName: "", account: "", password: "" },
      type: {
        login: "error",
        regist: "error",
        pwd: "error",
        purchase: "error",
      },
      Reserve: {
        productName: "",
        amount: "699",
        currency: "TWD",
        orderId: Math.floor(Math.random() * 1000000),
      },
      payType: "LinePay",
      url: "https://taipei-tour.herokuapp.com/taipei/memberSetting",
      purchase: {},
      pwd: { old: "", new: "" },
      rules: {
        checkPwd: (value) =>
          value == (this.regist.password || this.pwd.new) || "密碼不相符",
        email: (v) =>
          (v && v.length >= 1 && /.+@.+\..+/.test(v)) || "請輸入正確的email",
        pwd: (v) => (v && v.length >= 1) || "請輸入密碼",
      },
    };
  },
  methods: {
    openDialog() {
      this.dialog = this.dialogInfos.dialog;
      if (this.dialogInfos.method == "register") this.tabFllow = 1;
      if (this.dialogInfos.method == "login") this.tabFllow = 0;
    },
    closeDialog() {
      this.dialog = this.dialogInfos.dialog = false;
      this.alert.login = false;
      this.alert.regist = false;
      this.alert.purchase = false;
      this.alert.pwd = false;
      this.$refs.form.reset();
      // this.$refs.regist.reset();
      // this.$refs.purchase.reset();
      // this.$refs.pwd.reset();
    },
    async influence(type) {
      if (type == "login") {
        if (!this.$refs.form.validate()) return;
        this.load.login = true;
        const login_data = (await tour.login(this.login)).data;
        this.type.login = login_data.success ? "success" : "error";
        localStorage.setItem("headerToken", login_data.token);
        localStorage.setItem("userId", login_data.userId);
        localStorage.setItem("userName", login_data.userName);
        localStorage.setItem("account", login_data.account);
        this.$store.commit("setUserInfo", login_data);
        this.alert.login = true;
        this.message.login = login_data.message;
        this.load.login = false;
        if (!login_data.error) setTimeout(() => this.closeDialog(), 1000);
      }
      if (type == "regist") {
        if (!this.$refs.form.validate()) return;
        this.load.regist = true;
        const regist_data = (await tour.regist(this.regist)).data;
        this.type.regist = regist_data.success ? "success" : "error";
        this.alert.regist = true;
        this.message.regist = regist_data.message;
        this.load.regist = false;
      }
      if (type == "purchase") {
        if (!this.userId) {
          this.alert.purchase = true;
          this.message.purchase = "請先登入會員";
          return;
        }
        if (!this.startTime) {
          this.alert.purchase = true;
          this.message.purchase = "請選擇出發日期";
          return;
        }
        this.load.purchase = true;
        const reserve_data = (
          await tour.reserve({
            confirmUrl: `${this.url}/?orderId=${this.Reserve.orderId}&amount=${this.Reserve.amount}&productName=${this.Reserve.productName}&payType=${this.payType}&StartTime=${this.startTime}&userId=${this.userId}&account=${this.account}`,
            ...this.Reserve,
          })
        ).data;
        // alert(JSON.stringify(this.userId));
        location.href = reserve_data.info.paymentUrl.web;
      }
      if (type == "pwd") {
        if (!this.$refs.form.validate()) return;
        this.load.pwd = true;
        const pwd_data = await (await tour.upPwd(this.userId, this.pwd)).data;
        this.type.pwd = pwd_data.success ? "success" : "error";
        this.alert.pwd = true;
        this.message.pwd = pwd_data.message;
        this.load.pwd = false;
      }
    },
  },
  computed: {
    ...mapGetters(["userId", "account"]),
  },
  watch: {
    "dialogInfos.dialog"() {
      if (this.dialogInfos.dialog) {
        this.openDialog();
      }
    },
    dialog() {
      if (!this.dialog) {
        this.closeDialog();
      }
    },
  },
};
</script>