import Vue from 'vue'
import VueRouter from 'vue-router'
import main from '../views/main.vue'
import building from '../views/building.vue'
import cat from '../views/cat.vue'
import flower from '../views/flower.vue'
import north from '../views/north.vue'
import memberSetting from '../views/memberSetting.vue'
import qrcode from '../views/qrcode.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    redirect: '/main'
  },
  {
    path: '/main',
    name: 'main',
    component: main
  },
  {
    path: '/building',
    name: 'building',
    component: building
  },
  {
    path: '/cat',
    name: 'cat',
    component: cat
  },
  {
    path: '/flower',
    name: 'flower',
    component: flower
  },
  {
    path: '/north',
    name: 'north',
    component: north
  },
  {
    path: '/memberSetting',
    name: 'memberSetting',
    component: memberSetting
  },
  {
    path: '/qrcode',
    name: 'qrcode',
    component: qrcode
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
