<template>
  <div>
    <!-- 沒有登入會員 -->
    <v-container v-if="!userId" class="pa-10" fluid>
      <v-card>
        <v-row no-gutters>
          <v-col cols="7">
            <v-img :src="require('../assets/oil.jpg')"></v-img>
          </v-col>
          <v-col cols="5" class="d-flex align-center flex-column">
            <img :src="require('../assets/hello.png')" width="400px" contain />

            <v-btn
              class="mb-10 white--text"
              width="400px"
              color="#AB47BC"
              @click="memberInfo('login')"
              >立即登入</v-btn
            >
            <v-btn
              class="white--text"
              width="400px"
              color="#D81B60"
              @click="memberInfo('register')"
              >立即註冊</v-btn
            >
          </v-col>
        </v-row>
      </v-card>
    </v-container>
    <!-- 有登入會員 -->
    <v-container v-if="userId" fluid class="pa-12">
      <v-btn
        v-if="!confirmId.userId"
        color="#"
        outlined
        depressed
        style="border-radius: 20px"
        @click="memberInfo('pwd')"
        ><v-icon class="mr-2">mdi-lock-clock</v-icon
        ><span style="font-size: 1.2em; font-weight: bold"
          >變更密碼</span
        ></v-btn
      >
      <v-row class="d-flex align-center mt-10 mx-16">
        <v-divider class="mr-5" />
        <v-icon class="mr-2" size="2.1em" color="#c86fc9"
          >mdi-star-circle-outline</v-icon
        >
        <h1 style="color: #c86fc9">
          {{ confirmId.userId ? "交易結果" : "訂單紀錄" }}
        </h1>
        <v-divider class="ml-5" />
      </v-row>
      <v-container
        fluid
        class="d-flex justify-center align-center flex-column mt-10"
      >
        <v-card
          v-for="(goods, i) in goodsInfo"
          :key="i"
          class="mb-10"
          width="60vw"
        >
          <v-container>
            <!-- 商品名稱 -->
            <v-row class="d-flex align-center"
              ><v-col cols="2"> <h2 style="color: #8e518d">商品名稱</h2></v-col
              ><v-col cols="10"
                ><span style="font-size: 1.2em; font-weight: bold"
                  >{{ goods.productName }}
                </span>
              </v-col>
            </v-row>
            <!-- 出發日期 -->
            <v-row class="d-flex align-center"
              ><v-col cols="2"> <h2 style="color: #8e518d">出發日期</h2></v-col
              ><v-col cols="10"
                ><span style="font-size: 1.2em; font-weight: bold"
                  >{{ goods.StartTime }}
                </span>
              </v-col>
            </v-row>

            <!-- 付款金額 -->
            <v-row class="d-flex align-center"
              ><v-col cols="2"> <h2 style="color: #8e518d">付款金額</h2></v-col
              ><v-col cols="10"
                ><span style="font-size: 1.2em; font-weight: bold"
                  >NT. {{ goods.amount }}
                </span>
              </v-col>
            </v-row>
            <!-- 付款方式 -->
            <v-row class="d-flex align-center">
              <v-col cols="2" class="d-flex">
                <h2 style="color: #8e518d">付款方式</h2></v-col
              ><v-col cols="10"
                ><span style="font-size: 1.2em; font-weight: bold">{{
                  goods.payType
                }}</span></v-col
              >
            </v-row>
            <!-- 付款時間 -->
            <v-row class="d-flex align-center"
              ><v-col cols="2"> <h2 style="color: #8e518d">付款時間</h2></v-col
              ><v-col cols="10"
                ><span style="font-size: 1.2em; font-weight: bold"
                  >{{ goods.CreateTime }}
                </span>
              </v-col>
            </v-row>
            <!-- 訂單編號 -->
            <v-row class="d-flex align-center mb-3"
              ><v-col cols="2"> <h2 style="color: #8e518d">訂單編號</h2></v-col
              ><v-col cols="10"
                ><span style="font-size: 1.2em; font-weight: bold"
                  >{{ goods.orderId }}
                </span>
              </v-col>
            </v-row>
            <v-btn
              v-if="!confirmId.userId"
              block
              :loading="load"
              color="#AB47BC"
              class="mb-2"
              @click="refund(goods)"
              ><span
                style="font-size: 1.2em; font-weight: bold"
                class="white--text"
                >退款</span
              ></v-btn
            >
            <v-alert v-model="result" dense text :type="type">{{
              goodsInfo.message
            }}</v-alert>
          </v-container>
        </v-card>
      </v-container>
    </v-container>
    <v-snackbar v-model="snackbar" right>{{ message }}</v-snackbar>
    <v-overlay :value="over">
      <v-progress-circular
        :size="250"
        :width="10"
        color="#f7c7db"
        indeterminate
      >
        <h2>交易進行中...</h2></v-progress-circular
      >
    </v-overlay>
    <Dialog :dialogInfos="dialogInfos" />
  </div>
</template>
<script>
import Dialog from "../components/Dialog";
import tour from "../service";
import { mapGetters } from "vuex";
export default {
  components: {
    Dialog,
  },
  mounted() {
    this.confirmId = this.$route.query;
    if (!this.confirmId.userId) this.lineRecord();
    else this.Confirm();
  },
  data() {
    return {
      dialogInfos: { dialog: false, method: "" },
      goodsInfo: [],
      ConfirmData: {
        amount: this.$route.query.amount,
        currency: "TWD",
      },
      confirmId: "",
      over: false,
      load: false,
      result: false,
      type: "error",
      snackbar: false,
      message: "",
    };
  },
  methods: {
    memberInfo(type) {
      if (type == "login") {
        this.dialogInfos["dialog"] = true;
        this.dialogInfos["method"] = "login";
      }
      if (type == "register") {
        this.dialogInfos["dialog"] = true;
        this.dialogInfos["method"] = "register";
      }
      if (type == "pwd") {
        this.dialogInfos["dialog"] = true;
        this.dialogInfos["method"] = "pwd";
      }
    },
    async Confirm() {
      const productList = this.$route.query;
      this.over = true;
      const confirm_data = (await tour.confirm(productList, this.ConfirmData))
        .data;
      if (!confirm_data.error) {
        this.goodsInfo.push(confirm_data.info);
        this.goodsInfo["message"] = confirm_data.message;
        this.over = false;
        this.type = "success";
        this.result = true;
      }
      // alert(JSON.stringify(this.goodsInfo));
    },
    async lineRecord() {
      const record_data = (await tour.record()).data;
      this.goodsInfo = record_data.searchInfo;
    },
    async refund(list) {
      const { transactionId, productName } = list;
      const cancel = confirm(`刪除訂單[${productName}],請確認後再進行操作`);
      if (cancel) {
        this.load = true;
        const refund_data = (
          await tour.refund({ transactionId: transactionId })
        ).data;
        this.snackbar = true;
        this.message = refund_data.message;
        this.load = false;
        this.lineRecord();
      }
    },
  },
  computed: {
    ...mapGetters(["userId"]),
  },
  watch: {
    $route() {
      this.confirmId = this.$route.query;
      this.goodsInfo.length = "";
      this.result = false;
      this.lineRecord();
    },
  },
};
</script>