import Api from './Api'

export default {
    login(Info) {
        return Api().post(`/tour/user/login`, Info)
    },
    regist(Info) {
        return Api().post(`/tour/user`, Info)
    },
    upPwd(id, Info) {
        return Api().put(`/tour/user/${id}`, Info)
    },
    reserve(Info) {
        return Api().post(`line/reserve`, Info)
    },
    confirm(query, Info) {
        return Api().post(`line/confirm?orderId=${query.orderId}&amount=${query.amount}&transactionId=${query.transactionId}&productName=${query.productName}&payType=${query.payType}&StartTime=${query.StartTime}&userId=${query.userId}&account=${query.account}`, Info)
    },
    record() {
        return Api().get(`line/search`)
    },
    refund(Info) {
        return Api().put(`line/refund`, Info)
    }
}